@import url(https://fonts.googleapis.com/css?family=Clicker+Script);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
button {
  border:2px solid white;
  color: white;
  padding: 15px 32px;
  background-color: #282c34;
  margin: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

h1 {
  width: calc(30px + 2vmin);
  margin-top: 10px;
  height: 40px;
  padding: 10px;
  padding-top: 1px;
  align-self: center;
  font-weight: normal;
  font-size: calc(30px + 2vmin);
  background-color: rgb(79, 58, 199);
  
}

h2 {
  width: calc(30px + 2vmin);
  padding: 10px;
  font-weight: normal;
  align-self: center;
  text-align: center;
  font-size: calc(30px + 2vmin);
  /* background-color: red; */


}
h4 {
  width: 500px;
  padding: 1px;
  margin: 10px;
  font-weight: normal;
  justify-self: center;
  /* background-color: orange; */
  text-align: center;
  font-size: calc(30px + 2vmin);

}
h3 {
  padding: 1px;
  width: calc(50px + 1vmin);
  margin: 5px;
  text-align: center;
  font-weight: normal;
  font-size: calc(10px + 1vmin);
  /* background-color: red; */
}
.row3 {
  width: calc(120px + 180px + 12vmin);
  height: 100px;
  display: flex;
  flex-direction:row;
  /* background-color: greenyellow; */
  align-items: initial;
}
.row {

  width: calc(120px + 180px + 12vmin);
  height: 100px;
  display: flex;
  text-align: left;
  flex-direction:row;
  /* background-color: bisque; */
}
.row2 {
  width: calc(300px + 50vmin);;
  height: 300px;
  margin:5vmin ;
  display: flex;
  justify-content: center;
  align-content: space-around;
  flex-direction:column;
  flex-wrap: wrap;
  /* background-color: red; */
  

}
.title{
  font-family: 'Clicker Script';
  align-self: center;
  font-weight: normal;
  font-size: calc(40px + 4vmin);
  margin: auto;

  color: white;
  position: absolute;
  padding: 15px 32px;
  /* background-color: pink; */
  /* margin: 10px; */
}

.menu{
  font-weight: normal;
  font-size: calc(8px + 2vmin);
  /* border:1px solid white; */
  color: white;
  margin-left: auto;
  text-align: center;
  margin-right: 15px;
  padding: 8px 20px;
  /* background-color: pink; */
  /* margin: 10px; */
}

.top{
  font-weight: normal;
  font-size: calc(8px + 2vmin);
  /* border:1px solid white; */
  color: white;
  margin-right: auto;
  text-align: center;
  margin-right: 15px;
  padding: 8px 20px;
  /* background-color: pink; */
  /* margin: 10px; */
}

.header{
  width: 100vw;
  height: 100px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  color: white;
  border-bottom: 1px solid;
  /* background-color: green; */
}
.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  align-content: space-around;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}




.App-link {
  color: #61dafb;
}



